import { Link } from 'gatsby';
import React from 'react';

const Paging = ({ next, previous }) => {
	return (
		<div className="container my-4">
			<div className="row">
				<div className="col">
					{next && (
						<Link className="btn" rel="next" to={next.url}>
							← {next.title}
						</Link>
					)}
				</div>
				<div className="col text-right">
					{previous && (
						<Link className="btn" rel="next" to={previous.url}>
							{previous.title} →
						</Link>
					)}
				</div>
			</div>
		</div>
	);
};

export default Paging;
